* {
  box-sizing: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a{
  color: #4d4dff;
  text-decoration: underline;
}

div.header {
  margin-bottom: 75px;
}

fieldset {
	padding: 25px;
  margin-bottom: 25px;
}

.modalScroll {
	position: absolute;

	width: calc(100% - 40px);
	height: calc(100% - 180px);
	

	overflow: auto;
}

div.buttons {
  float: right;
}

div.search { 
  display: flex;
  margin-bottom: 30px;
}